@import "../../styles/sass/variables";

.title {
  position: relative;
  margin: 0 0 40px;
  color: $textColor;
  text-transform: capitalize;
  font-size: 36px;
  line-height: 1.2;
  letter-spacing: 5px;

  &::before {
    content: '';
    position: absolute;
    background: $primaryColor;
    width: 55px;
    height: 1px;
    bottom: -5px;
    left: 50%;
    transform: translate(-50%, 50%);
  }

  h2 {
    font: 400 40px/1.2 'Josefin Sans', sans-serif;
    margin: 0 0 10px;
  }
}



